import React from 'react';
import { ReactComponent as X } from '../../../../img/icons/x.svg';
import { useNavigate } from 'react-router-dom';
import { BtnBasket, HeartBtn, btnHOC } from '../../../../components';
import { links } from '../../../../Router';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { removeBasketHTTP } from '../../../../store/goodsSlices/basketSlice';

import './style/ProductBasket.scss';
import { ProductBasketT } from '../../../../store/goodsSlices/goods.config';

export const ProductBasket = ({ product }: TypeGoods) => {
  const dispatch = useAppDispatch();
  const { sale } = useAppSelector((state) => state.basket);
  const router = useNavigate();
  const Btn = btnHOC<ProductBasketT>(BtnBasket);

  const clean = async () => {
    await dispatch(removeBasketHTTP({ id: product.id }));
  };

  const route = () => {
    router(`/basket/${product.id}`);
    document.documentElement.scrollIntoView(true);
  };

  return (
    <div className='productbasket__item'>
      <div className='productbasket__first'>
        <div className='productbasket__blockimg' onClick={route}>
          <div
            className='productbasket__img '
            style={{
              backgroundImage: `url(${links.API_URL}${
                Array.from(
                  Object.values(JSON.parse(product.deviceimg.img || '{}'))
                )[0]
              })`,
            }}
          ></div>
        </div>
      </div>

      <div className='productbasket__second'>
        <div className='productbasket__text'>
          <div className='productbasket__name'>
            <p className='basketblock'>
              {product?.name.length <= 15
                ? product?.name
                : product?.name
                    .split('')
                    .filter((_, index) => index <= 14)
                    .join('')}
              {product?.name.length > 15 && (
                <span
                  title={product?.name
                    .split('')
                    .filter((_, index) => index > 14)
                    .join('')}
                >
                  ...
                </span>
              )}
            </p>
          </div>
          <div className='productbasket__category'>
            <p className='mini basketblock'>{product?.brand.name}</p>
          </div>
        </div>
        <div className='productbasket__btnfirst'>
          <Btn product={product} />
        </div>
      </div>
      <div className='productbasket__btnsecond'>
        <Btn product={product} />
      </div>
      <div className='productbasket__midst'>
        <div className='productbasket__price'>
          {sale != 0 && (
            <p className='big sale'>{`$${
              +product?.price * product.quantity -
              ((+product?.price * product.quantity) / 100) *
                (sale + +product?.sale)
            }`}</p>
          )}
          {product.sale > 0 && sale <= 0 ? (
            <>
              <p className={'basketblock newcols big'}>{`$${(
                +product?.price * product?.quantity -
                ((+product?.price * product?.quantity) / 100) * product.sale
              ).toFixed(0)}`}</p>
              <p className={'basketblock through mini'}>{`$${
                +product?.price * product?.quantity
              }`}</p>
            </>
          ) : (
            <p
              className={sale != 0 ? 'mini old basketblock' : 'big basketblock'}
            >{`$${+product?.price * product?.quantity}`}</p>
          )}
        </div>
      </div>

      <div className='productbasket__end'>
        <HeartBtn id={product.id} />
        <div onClick={clean} className='productbasket__delete'>
          <X />
        </div>
      </div>
    </div>
  );
};

export interface TypeGoods {
  product: ProductBasketT;
}
